import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import ButtonBase from "@material-ui/core/ButtonBase";
import Button from "@material-ui/core/Button";
import { CircularProgress } from "@material-ui/core";
import moment from "moment";
// import { jobs } from './jobs';
import axios from "axios";
import { Link } from "react-router-dom";
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    margin: "auto",
    maxWidth: 450,
    maxHeight: 180,
  },
  image: {
    width: 128,
    height: 128,
  },
  text: {
    textAlign: "center",
    justifyContent: "center",
  },
  img: {
    margin: "auto",
    display: "block",
    maxWidth: "100%",
    maxHeight: "100%",
  },
  jobPaper: {
    margin: "10px 10px 10px 10px",
  },
  linker: {
    color: "inherit",
    textDecoration: "none",
  },
  salary: {
    color: "#4ee44e",
  },
}));
const fixDate = (list) => {
  let returnValue = [];
  var oldDate1;
  var oldDate2;
  for (var i = 0; i < list.length; i++) {
    oldDate1 = new Date(list[i].deadline);
    oldDate1 = moment(oldDate1).format("HH:mm , D-MM-YYYY");
    list[i].deadline = oldDate1;
    oldDate2 = new Date(list[i].postedOn);
    oldDate2 = moment(oldDate2).format("HH:mm , DD-MM-YYYY");
    list[i].postedOn = oldDate2;
    returnValue.push(list[i]);
  }
  return returnValue;
};

export default function JobCard() {
  const classes = useStyles();
  const [jobs, setJobs] = React.useState([]);
  const [loading, setLoading] = React.useState(true);
  const [error, setError] = React.useState(false);
  const [errorMessage, setErrorMessage] = React.useState("");

  React.useEffect(() => {
    // fetch jobs from /api/list/ using axios post with search as deadline
    // if (localStorage.getItem("type") !== "A") window.location.href = "/404";

    axios
      .post("/api/list/", { cond: { deadline: { $gte: new Date() } } })
      .then((res) => {
        var fixed = fixDate(res.data);
        console.log(fixed);
        setJobs(fixed);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
        setLoading(false);
        setError(true);
        setErrorMessage(err.response.data.message);
      });
  }, []);

  if (loading) {
    return (
      <div className={classes.root}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Paper className={classes.paper}>
              <Typography
                style={{
                  textAlign: "center",
                  display: "flex",
                  justifyContent: "center",
                  placeItems: "center",
                }}
                variant="h5"
                component="h3"
              >
                Loading...
              </Typography>
            </Paper>
            <Grid item xs={12}>
              <Typography
                style={{
                  textAlign: "center",
                  display: "flex",
                  justifyContent: "center",
                  placeItems: "center",
                }}
                variant="h5"
                component="h3"
              >
                <CircularProgress />
              </Typography>
            </Grid>
          </Grid>
        </Grid>
      </div>
    );
  }

  if (error) {
    return (
      <div className={classes.root}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Paper className={classes.paper}>
              <Typography variant="h5" component="h3">
                {errorMessage}
              </Typography>
            </Paper>
          </Grid>
        </Grid>
      </div>
    );
  }

  if (jobs.length === 0 && !loading && !error) {
    return (
      <div className={classes.root}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <Paper className={classes.paper}>
              <Typography variant="h5" component="h3">
                No jobs available
              </Typography>
            </Paper>
          </Grid>
        </Grid>
      </div>
    );
  }
  // Sort jobs by deadline and return the first 10 jobs
  const sortedJobs = jobs.sort((a, b) => {
    if (a.deadline < b.deadline) {
      return 1;
    } else if (a.deadline > b.deadline) {
      return -1;
    } else {
      return 0;
    }
  });

  return (
    <div className={classes.root}>
      <Typography variant="h4" className={classes.text}>
        Recent Job listings.
      </Typography>
      <Grid container>
        {sortedJobs.map((job) => {
          // const name = job.title.replace(/ /g, "-").toLowerCase();
          // const jobid = job._id.slice(-4);
          return (
            <Grid item key={job._id} className={classes.jobPaper}>
              <Paper className={classes.paper}>
                <Grid container spacing={2}>
                  <Grid item>
                    <ButtonBase className={classes.image}>
                      <img
                        className={classes.img}
                        alt="complex"
                        src={"/assets/images/rupandehi.jpg"}
                      />
                    </ButtonBase>
                  </Grid>
                  <Grid item xs={12} sm container>
                    <Grid item xs container direction="column" spacing={2}>
                      <Grid item xs>
                        <Typography
                          color="primary"
                          gutterBottom
                          variant="subtitle1"
                        >
                          {job.title}
                        </Typography>
                        <Typography variant="body2" gutterBottom>
                          {job.type}
                        </Typography>
                        <Typography variant="body2" color="textSecondary">
                          Level: {job.level}
                        </Typography>
                      </Grid>
                      <Grid item>
                        <Link
                          className={classes.linker}
                          to={
                            // `/jobs/${name}-${jobid}`
                            `/jobs/${job._id}`
                          }
                        >
                          <Button
                            size="small"
                            color="secondary"
                            variant="outlined"
                          >
                            View Job
                          </Button>
                        </Link>
                      </Grid>
                    </Grid>
                    <Grid item>
                      <Typography
                        variant="body2"
                        style={{ marginLeft: "5px" }}
                        color="textSecondary"
                      >
                        Salary:
                      </Typography>
                      <Typography
                        variant="subtitle1"
                        className={classes.salary}
                      >
                        {job.salary}
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              </Paper>
            </Grid>
          );
        })}
      </Grid>
    </div>
  );
}
