import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import ButtonBase from "@material-ui/core/ButtonBase";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";
import CircularProgress from "@material-ui/core/CircularProgress";
import axios from "axios";
import moment from "moment";
import { Link } from "react-router-dom";
// import { jobs } from "../JobCard/jobs";
const useStyles = makeStyles((theme) => ({
  root: {
    display: "grid",
    minHeight: "70vh",
    marginTop: "20px",
    padding: "0px 20px",
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    margin: "auto",
    maxWidth: 450,
    maxHeight: 180,
  },
  image: {
    width: 128,
    height: 128,
  },
  text: {
    textAlign: "center",
    justifyContent: "center",
  },
  img: {
    margin: "auto",
    display: "block",
    maxWidth: "100%",
    maxHeight: "100%",
  },
  jobPaper: {
    margin: "10px 10px 10px 10px",
  },
  linker: {
    color: "inherit",
    textDecoration: "none",
  },
  salary: {
    color: "#4ee44e",
  },
}));

export default function Jobs() {
  const classes = useStyles();
  const [jobs, setJobs] = React.useState([]);
  const [loading, setLoading] = React.useState(false);
  const [error, setError] = React.useState(null);
  const [errorMessage, setErrorMessage] = React.useState(null);
  const fixDate = (list) => {
    let returnValue = [];
    var oldDate1;
    var oldDate2;
    for (var i = 0; i < list.length; i++) {
      oldDate1 = new Date(list[i].deadline);
      oldDate1 = moment(oldDate1).format("HH:mm , D-MM-YYYY");
      list[i].deadline = oldDate1;
      oldDate2 = new Date(list[i].postedOn);
      oldDate2 = moment(oldDate2).format("HH:mm , DD-MM-YYYY");
      list[i].postedOn = oldDate2;
      returnValue.push(list[i]);
    }
    return returnValue;
  };

  React.useEffect(() => {
    // fetch jobs from /api/list/ using axios post with search as deadline
    // if (localStorage.getItem("type") !== "A") window.location.href = "/404";

    axios
      .post("/api/list/", { cond: { deadline: { $gte: new Date() } } })
      .then((res) => {
        var fixed = fixDate(res.data);
        console.log(fixed);
        setJobs(fixed);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
        setLoading(false);
        setError(true);
        setErrorMessage(err.response);
      });
  }, []);
  if (loading) {
    return (
      <div className={classes.root}>
        <Box p={4}>
          <Typography variant="h4" color="textSecondary">
            Loading...
            <CircularProgress />
          </Typography>
        </Box>
      </div>
    );
  }
  if (error) {
    return (
      <div className={classes.root}>
        <Box p={4}>
          <Typography variant="h4" color="textSecondary">
            Error
          </Typography>
          <Typography variant="h5" color="textSecondary">
            {errorMessage}
          </Typography>
        </Box>
      </div>
    );
  }

  return (
    <div className={classes.root}>
      <Typography variant="h4" className={classes.text}>
        Recent Job Openings.
      </Typography>
      <Grid container>
        {jobs.map((job) => {
          // const name = job.title.replace(/ /g, "-").toLowerCase();
          // const jobid = job._id.slice(-4);
          return (
            <Grid item key={job.name} className={classes.jobPaper}>
              <Paper className={classes.paper}>
                <Grid container spacing={2}>
                  <Grid item>
                    <ButtonBase className={classes.image}>
                      <img
                        className={classes.img}
                        alt="complex"
                        src={"/assets/images/rupandehi.jpg"}
                      />
                    </ButtonBase>
                  </Grid>
                  <Grid item xs={12} sm container>
                    <Grid item xs container direction="column" spacing={2}>
                      <Grid item xs>
                        <Typography
                          color="primary"
                          gutterBottom
                          variant="subtitle1"
                        >
                          {job.title}
                        </Typography>
                        <Typography variant="body2" gutterBottom>
                          {job.type}
                        </Typography>
                        <Typography variant="body2" color="textSecondary">
                          Level: {job.level}
                        </Typography>
                      </Grid>
                      <Grid item>
                        <Link
                          className={classes.linker}
                          to={
                            // `/jobs/${name}-${jobid}`
                            `/jobs/${job._id}`
                          }
                        >
                          <Button
                            size="small"
                            color="secondary"
                            variant="outlined"
                          >
                            View Job
                          </Button>
                        </Link>
                      </Grid>
                    </Grid>
                    <Grid item>
                      <Typography
                        variant="body2"
                        style={{ marginLeft: "5px" }}
                        color="textSecondary"
                      >
                        Salary:
                      </Typography>
                      <Typography
                        variant="subtitle1"
                        className={classes.salary}
                      >
                        {job.salary}
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
              </Paper>
            </Grid>
          );
        })}
      </Grid>
    </div>
  );
}
